.tech-stack {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 8% 8%;
  position: relative;
  overflow-x: hidden;
}

.knowledge-graph {
  position: relative;
  width: 100%;
  max-width: 800px;
  margin: 1.5rem auto 0;
  aspect-ratio: 1;
  overflow: hidden;
  background: transparent;
}

.circular-ring {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: rotateRing linear infinite;
  overflow: hidden;
  background: transparent;
  /* GPU acceleration hints for better performance */
  will-change: transform;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.circular-ring-0 {
  animation-duration: 30s;
}

.circular-ring-1 {
  animation-duration: 35s;
}

.circular-ring-2 {
  animation-duration: 40s;
}

.circular-ring-3 {
  animation-duration: 45s;
}

.circular-ring-4 {
  animation-duration: 50s;
}

.circular-ring-5 {
  animation-duration: 55s;
}

@keyframes rotateRing {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.circular-text-container {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: transparent;
}

.circular-text {
  font-family: 'Inter', sans-serif;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  paint-order: stroke fill;
  dominant-baseline: middle;
  overflow: hidden;
  background: transparent;
  /* Optimize text rendering */
  text-rendering: optimizeSpeed;
  shape-rendering: optimizeSpeed;
}

.circular-text-cyan {
  fill: #22d3ee;
}

.circular-text-purple {
  fill: #c084fc;
}

.circular-text-blue {
  fill: #3b82f6;
}

.circular-text-teal {
  fill: #10b981;
}

.circular-text-orange {
  fill: #ff6b35;
}

.circular-text-pink {
  fill: #ec4899;
}

/* Category Legend */
.category-legend {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 1.5rem;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  backdrop-filter: blur(10px);
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  transition: all 0.3s ease;
  opacity: 0.6;
  padding: 0.5rem 1rem;
  border-radius: 50px;
}

.legend-item.legend-active {
  opacity: 1;
  background: rgba(255, 255, 255, 0.05);
  transform: scale(1.05);
}

.legend-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  display: block;
}

.legend-cyan {
  background: #22d3ee;
  box-shadow: 0 0 12px #22d3ee;
}

.legend-purple {
  background: #c084fc;
  box-shadow: 0 0 12px #c084fc;
}

.legend-blue {
  background: #3b82f6;
  box-shadow: 0 0 12px #3b82f6;
}

.legend-teal {
  background: #10b981;
  box-shadow: 0 0 12px #10b981;
}

.legend-orange {
  background: #ff6b35;
  box-shadow: 0 0 12px #ff6b35;
}

.legend-pink {
  background: #ec4899;
  box-shadow: 0 0 12px #ec4899;
}

.legend-label {
  font-size: 0.9rem;
  color: var(--text-secondary);
  font-weight: 500;
}

/* Responsive */
@media (max-width: 1024px) {
  .knowledge-graph {
    max-width: 900px;
  }
  
  .circular-text {
    letter-spacing: 0.1em;
    font-weight: 700;
  }
}

@media (max-width: 768px) {
  .tech-stack {
    padding: 15% 5%;
    overflow-x: hidden;
  }

  .knowledge-graph {
    max-width: 95vw;
    width: 95vw;
    margin: 1.5rem auto;
    min-height: 95vw;
    max-height: 95vw;
  }
  
  .circular-text {
    letter-spacing: 0.18em;
    font-weight: 900;
  }

  .category-legend {
    flex-wrap: wrap;
    justify-content: center;
    gap: 0.4rem;
    margin-top: 1.5rem;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    padding: 0.5rem 0.3rem;
    max-width: calc(100vw - 1rem);
  }

  .legend-item {
    padding: 0.3rem 0.5rem;
    gap: 0.4rem;
  }

  .legend-dot {
    width: 8px;
    height: 8px;
  }

  .legend-label {
    font-size: 0.7rem;
  }
}
