.certifications {
  width: 100%;
  max-width: 100vw;
  overflow: hidden;
  padding: 6rem 0;
  background: transparent;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}

.certifications-label {
  text-align: center;
  margin-bottom: 3rem;
  z-index: 3;
  position: relative;
}

.certifications-label .section-number {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--accent-color);
  letter-spacing: 0.2em;
  display: block;
  margin-bottom: 0.5rem;
}

.certifications-label h2 {
  font-size: 1.5rem;
  font-weight: 600;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  color: rgba(255, 255, 255, 0.7);
  letter-spacing: 0.15em;
  text-transform: uppercase;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
}

.certifications-track {
  display: flex;
  align-items: center;
  width: 100%;
}

.certifications-content {
  display: flex;
  align-items: center;
  gap: 2.5rem;
  padding: 0 2rem;
}

.cert-item {
  flex-shrink: 0;
  height: 120px;
  width: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.8;
  transition: all 0.3s ease;
}

.cert-item-large {
  height: 140px;
  width: 140px;
}

.cert-item:hover {
  opacity: 1;
  transform: scale(1.15);
}

.cert-item img {
  height: 100%;
  width: 100%;
  object-fit: contain;
  filter: brightness(1.1);
}

.cert-item-large img {
  height: 110%;
  width: 110%;
}

@media (max-width: 768px) {
  .certifications {
    padding: 4rem 0;
  }
  
  .certifications-content {
    gap: 1.5rem;
    padding: 0 1rem;
  }

  .cert-item {
    height: 80px;
    width: 80px;
  }

  .cert-item-large {
    height: 100px;
    width: 100px;
  }

  .cert-item-large img {
    height: 115%;
    width: 115%;
  }

  .certifications-label h2 {
    font-size: 1.2rem;
  }
}
