.companies {
  width: 100%;
  max-width: 100vw;
  overflow: hidden;
  padding: 6rem 0;
  background: transparent;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}

.companies-label {
  text-align: center;
  margin-bottom: 3rem;
  z-index: 3;
  position: relative;
}

.companies-label .section-number {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--accent-color);
  letter-spacing: 0.2em;
  display: block;
  margin-bottom: 0.5rem;
}

.companies-label h2 {
  font-size: 1.5rem;
  font-weight: 600;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  color: rgba(255, 255, 255, 0.7);
  letter-spacing: 0.15em;
  text-transform: uppercase;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
}

.companies-track {
  display: flex;
  align-items: center;
  width: 100%;
}

.companies-content {
  display: flex;
  align-items: center;
  gap: 8rem;
  padding: 0 4rem;
}

.company-item {
  flex-shrink: 0;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.7;
  transition: all 0.3s ease;
}

.company-item-medium {
  height: 60px;
}

.company-item-large {
  height: 70px;
}

.company-item-xlarge {
  height: 85px;
}

.company-item:hover {
  opacity: 1;
  transform: scale(1.1);
}

.company-item img {
  height: 100%;
  width: auto;
  max-width: 200px;
  object-fit: contain;
  filter: brightness(1.2);
}

.company-item-medium img {
  max-width: 220px;
}

.company-item-large img {
  max-width: 250px;
}

.company-item-xlarge img {
  max-width: 280px;
}

@media (max-width: 768px) {
  .companies {
    padding: 2rem 0;
  }
  
  .companies-content {
    gap: 4rem;
    padding: 0 2rem;
  }
  
  .company-item {
    height: 40px;
  }
  
  .company-item img {
    max-width: 100px;
  }
}
