.work-experience {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 8% 8%;
  position: relative;
}

.experience-timeline {
  margin-top: 4rem;
  position: relative;
}

.experience-item {
  position: relative;
  padding-left: 4rem;
  margin-bottom: 4rem;
}

.experience-item:last-child {
  margin-bottom: 0;
}

.experience-line {
  position: absolute;
  left: 0;
  top: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, var(--accent-color), var(--accent-secondary));
  opacity: 0.3;
}

.experience-line::before {
  content: '';
  position: absolute;
  left: -6px;
  top: 0;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background: var(--accent-color);
  box-shadow: 0 0 20px rgba(6, 182, 212, 0.5);
}

.experience-content {
  background: linear-gradient(135deg, rgba(6, 182, 212, 0.08), rgba(124, 58, 237, 0.08));
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.12);
  border-radius: 8px;
  padding: 2.5rem;
  transition: all 0.4s ease;
}

.experience-item:hover .experience-content {
  background: linear-gradient(135deg, rgba(6, 182, 212, 0.15), rgba(124, 58, 237, 0.15));
  border-color: rgba(6, 182, 212, 0.3);
  transform: translateX(10px);
}

.experience-header {
  display: flex;
  align-items: flex-start;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}

.experience-logo {
  flex-shrink: 0;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  padding: 0.75rem;
}

.experience-logo-large {
  width: 80px;
  height: 80px;
  padding: 0.5rem;
}

.experience-logo-xlarge {
  width: 90px;
  height: 90px;
  padding: 0.25rem;
}

.experience-logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  filter: brightness(1.2);
}

.experience-title-group {
  flex: 1;
}

.experience-title-group h3 {
  font-size: 1.5rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.95);
  margin-bottom: 0.5rem;
}

.experience-meta {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex-wrap: wrap;
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.65);
}

/* Show desktop version, hide mobile version on desktop */
.experience-meta-desktop {
  display: flex;
}

.experience-meta-mobile {
  display: none;
}

.experience-meta .company {
  color: var(--accent-color);
  font-weight: 500;
}

.experience-meta .separator {
  opacity: 0.5;
}

.experience-description {
  list-style: none;
  padding: 0;
  margin: 0;
}

.experience-description li {
  position: relative;
  padding-left: 1.5rem;
  margin-bottom: 0.75rem;
  line-height: 1.8;
  color: rgba(255, 255, 255, 0.75);
}

.experience-description li::before {
  content: '▹';
  position: absolute;
  left: 0;
  color: var(--accent-color);
  font-size: 1.2rem;
}

.experience-description li:last-child {
  margin-bottom: 0;
}

@media (max-width: 768px) {
  .work-experience {
    padding: 15% 5%;
  }

  .experience-item {
    padding-left: 2.5rem;
    margin-bottom: 3rem;
  }

  .experience-content {
    padding: 1.5rem;
  }

  .experience-header {
    flex-wrap: nowrap;
    gap: 1rem;
    align-items: flex-start;
    margin-bottom: 1rem;
  }

  .experience-logo {
    width: 75px;
    height: 75px;
  }

  .experience-logo-large {
    width: 75px;
    height: 75px;
  }

  .experience-logo-xlarge {
    width: 75px;
    height: 75px;
    padding: 0.25rem;
  }

  .experience-title-group {
    flex: 1;
    min-width: 0;
  }

  .experience-title-group h3 {
    font-size: 1.125rem;
    margin-bottom: 0;
  }

  /* Hide desktop version, show mobile version on mobile */
  .experience-meta-desktop {
    display: none;
  }

  .experience-meta-mobile {
    display: flex;
    width: 100%;
    margin-bottom: 1rem;
    padding-left: 0;
    margin-left: 0;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 0.5rem;
  }

  .experience-title-group h3 {
    font-size: 1.25rem;
  }

  .experience-meta .separator {
    display: none;
  }
}
