.expertise {
  min-height: 400vh;
  position: relative;
  padding: 0;
}

.expertise-content-wrapper {
  position: sticky;
  top: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 8% 8% 8% 8%;
}

.expertise .section-header {
  position: relative;
  margin-bottom: 4rem;
  z-index: 100;
  opacity: 1 !important;
  visibility: visible !important;
  align-self: flex-start;
}

.expertise-scroll-container {
  flex: 1;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.expertise-row {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  pointer-events: none;
}

.expertise-row.active {
  pointer-events: auto;
}

.expertise-icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.icon-wrapper {
  width: 35rem;
  height: 35rem;
  color: var(--accent-color);
  filter: drop-shadow(0 0 40px rgba(6, 182, 212, 0.5));
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-wrapper svg {
  width: 100%;
  height: 100%;
}

.expertise-card {
  background: transparent;
  backdrop-filter: none;
  border: none;
  border-radius: 0;
  padding: 3rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: none;
}

.expertise-card h3 {
  font-size: 4rem;
  font-weight: 700;
  font-family: 'Canela', serif;
  color: rgba(255, 255, 255, 1);
  margin-bottom: 1.5rem;
  line-height: 1;
}

.expertise-card p {
  font-size: 1.125rem;
  line-height: 1.8;
  color: rgba(255, 255, 255, 0.9);
}

.expertise-card::before {
  display: none;
}

.expertise-card:hover::before {
  display: none;
}

.expertise-card:hover {
  transform: none;
}

@media (max-width: 1200px) {
  .icon-wrapper {
    width: 25rem;
    height: 25rem;
  }
  
  .expertise-card {
    padding: 2.5rem;
  }
  
  .expertise-card h3 {
    font-size: 3rem;
  }
}

@media (max-width: 768px) {
  .expertise {
    min-height: auto;
    padding: 0;
  }

  .expertise-content-wrapper {
    position: relative;
    height: auto;
    padding: 15% 5%;
  }

  .expertise-scroll-container {
    min-height: auto;
    display: block;
  }

  .expertise-row {
    position: relative;
    transform: none;
    top: auto;
    grid-template-columns: 1fr;
    gap: 2rem;
    min-height: auto;
    margin-bottom: 3rem;
    opacity: 1 !important;
    pointer-events: auto !important;
  }

  .expertise-icon-container {
    height: 200px;
  }

  .icon-wrapper {
    width: 15rem;
    height: 15rem;
  }

  .expertise-card {
    padding: 2rem 1.5rem;
    min-height: auto;
  }

  .expertise-card h3 {
    font-size: 2rem;
    margin-bottom: 1rem;
  }

  .expertise-card p {
    font-size: 1rem;
  }
}
